@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

* {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background: linear-gradient(#EE9CA7, #FFDDE1);
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App {
  text-align: center;
}

h1 {
  color: #fff;
  margin-bottom: 0.5rem;
  font-size: 2rem;
  font-family: 'Times New Roman', Times, serif;
}

.TodoWrapper {
  background: #f4978e;
  margin-top: 5rem;
  padding: 2rem;
  border-radius: 15px;
}

.TodoForm {
  width: 100%;
  margin-bottom: 0.2rem;
}

.TodoInput {
  outline: none;
  background: none;
  border: 1px solid #ffdab9;
  padding: 0.5rem 1rem;
  margin-top: 1rem;
  margin-bottom: 2rem;
  width: 300px;
  color: #fff;
}

.TodoInput::placeholder {
  color: #fff;
}

.TodoBtn {
  background: #ffdab9;
  color: #c38e70;
  border: none;
  padding: 0.55rem;
  cursor: pointer;
}

.Todo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: #f8ad9d;
  color: #fff;
  padding: 0.75rem 1rem;
  border-radius: 5px;
  margin-bottom: 1rem;
}

.fa-trash {
  margin-left: 0.75rem;
}

.incompleted {
  color: #fff;
  cursor: pointer;
}

.completed {
  color: #aeffb9;
  text-decoration: line-through;
  cursor: pointer;
}

.edit-icon,
.delete-icon {
  cursor: pointer;
}
 